  content: "";
  position: absolute;
  left: 14px;
  width: 20px;
  height: 3px;
  border-radius: 3px;
  background-color: #ffffff;
  transition: transform 0.2s ease-in-out, top 0.2s ease-in-out, bottom 0.2s ease-in-out;
}
